@import '../../../base';

.product-slider {
  margin-top: 20px;
}

.step-progress-description-container {
  // margin-top: 40px;
  max-width: 1036px;
  padding: 0 15px;
  margin-left: auto;
  margin-right: auto;

  @media screen and (max-width: 576px) {
    margin-top: 0;
  }

  .main {
    min-height: 36px;
    text-align: center;
    font-size: $font-size-big;
    font-weight: $font-weight-heavy;
    color: $color-title;
    margin: 0;

    @media screen and (max-width: 576px) {
      min-height: auto;
    }

    @media screen and (max-width: 690px) {
      font-size: $font-size-regular;
    }
  }

  .sub {
    font-size: $font-size-regular;
    font-weight: $font-weight-normal;
    text-align: center;
    color: $color-body;
  }

  .back-button {
    position: absolute;
    left: 15px;
    font-size: $font-size-big;
    font-weight: $font-weight-bold;
    color: $color-caption;

    @media screen and (max-width: 576px) {
      position: relative;
    }

    &:hover {
      text-decoration: none;
      color: $color-hover;
    }
  }
}
